@import url(https://fonts.googleapis.com/css2?family=Athiti:wght@400;500;600;700&family=Barlow:wght@400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--y: #FEBD2E;--w: #FBFCFF;--b: #242424;--p: #695327}body{font-family:'Barlow', 'Athiti', sans-serif;font-size:16px;color:#242424;background-color:#F5F3F0;overflow:hidden;touch-action:none;-ms-touch-action:none}a{color:#242424;color:var(--b);text-decoration:none}.noselect{-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.App .container{display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;height:calc(1vh * 100);height:calc(var(--vh, 1vh) * 100);width:100vw}.App nav{height:100%;width:8%;max-width:120px;display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap}.App nav div,.App nav a{height:var(--navw);width:100%;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}.App section{height:100%;-webkit-flex:1 1;flex:1 1}.App #main-ico{font-size:calc(var(--navw) * 0.40)}.App #nav-ico{font-size:calc(var(--navw) * 0.25);width:1em;height:1em;color:#242424;color:var(--b)}.App .btn{background-color:#FBFCFF;background-color:var(--w);width:60%;height:calc(var(--navw) * 0.60);border-radius:10px;border:#E2DDD3 solid 1px;transition:background 0s}.App .btn.selected{background-color:#FEBD2E;background-color:var(--y);transition:background 0.75s}.App .btn:not(.selected):hover{width:65%;height:calc(var(--navw) * 0.65);border-radius:12px}

.Home{font-family:'Poppins', sans-serif}.Home .circle{background-color:white;width:70vw;height:70vw;border-radius:100%;position:absolute;-webkit-transform:translate(-50%, -60%);transform:translate(-50%, -60%);left:75vw;-webkit-animation:0.5s ease-out 0s 1 glow;animation:0.5s ease-out 0s 1 glow}@media (max-aspect-ratio: 16 / 8){.Home .circle{width:80vw;height:80vw;-webkit-transform:translate(-50%, -60%);transform:translate(-50%, -60%);-webkit-animation:0.5s ease-out 0s 1 glow2;animation:0.5s ease-out 0s 1 glow2}}@-webkit-keyframes glow{0%{opacity:0;width:0vw;height:0vw}100%{opacity:1;width:70vw;height:70vw}}@keyframes glow{0%{opacity:0;width:0vw;height:0vw}100%{opacity:1;width:70vw;height:70vw}}@-webkit-keyframes glow2{0%{opacity:0;width:0vw;height:0vw}100%{opacity:1;width:80vw;height:80vw}}@keyframes glow2{0%{opacity:0;width:0vw;height:0vw}100%{opacity:1;width:80vw;height:80vw}}.Home #svg-icon{position:absolute;-webkit-transform:translate(-100%, -100%);transform:translate(-100%, -100%);top:calc(1vh * 100);top:calc(var(--vh, 1vh) * 100);left:100vw;-webkit-animation:0.8s ease-out 0s 1 slideIn;animation:0.8s ease-out 0s 1 slideIn}.Home #svg-icon svg{--scale: 40;height:calc(1vh * var(--scale));height:calc(var(--vh, 1vh) * var(--scale));width:calc(784 * (1vh * var(--scale) / 702));width:calc(784 * (var(--vh, 1vh) * var(--scale) / 702))}@-webkit-keyframes slideIn{0%{left:150vw}60%{left:150vw}100%{left:100vw}}@keyframes slideIn{0%{left:150vw}60%{left:150vw}100%{left:100vw}}.Home>.content{width:25%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;position:fixed;-webkit-transform:translateX(-50%);transform:translateX(-50%);left:calc(var(--navw) + 20%);margin-top:calc(var(--navw) * 2.2);-webkit-animation:0.25s ease-out 0s 1 fadeIn;animation:0.25s ease-out 0s 1 fadeIn}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.Home>.content h1{color:var(--y);font-weight:600;font-size:2.75vw;margin:0}.Home>.content h2{font-weight:500;font-size:2.5vw;line-height:3.5vw;margin:0}.Home>.content p{font-size:1.025vw}.Home>.content .order-btn{background-color:var(--y);font-weight:700;font-size:1.025vw;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;padding:0.95vw 3vw 0.95vw 3vw;border-radius:3vw}

.Content{padding-left:calc(var(--navw) * 0.5);-webkit-animation:0.25s ease-out 0s 1 fadeIn;animation:0.25s ease-out 0s 1 fadeIn}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.Content>header{padding-top:calc(var(--navw) * 0.25);position:fixed;z-index:2}.Content>header h1{margin:0;font-size:1.75vw;font-weight:600;line-height:2.5vw}.Content>header h2{margin:0;font-size:1vw;font-weight:400;color:var(--p)}.Content .controller{position:fixed;z-index:2;background-color:var(--w);width:calc(30vw - var(--navw) * 0.8);height:calc(100% - var(--navw) * 0.6);right:0;top:0;-webkit-animation:0.2s ease-out 0s 1 slideIn2;animation:0.2s ease-out 0s 1 slideIn2;padding-top:calc(var(--navw) * 0.35);padding-bottom:calc(var(--navw) * 0.25);padding-left:calc(var(--navw) * 0.4);padding-right:calc(var(--navw) * 0.4);display:-webkit-flex;display:flex;-webkit-flex-flow:column nowrap;flex-flow:column nowrap;-webkit-justify-content:space-between;justify-content:space-between}@-webkit-keyframes slideIn2{0%{right:-35vw}50%{right:-35vw}100%{right:0}}@keyframes slideIn2{0%{right:-35vw}50%{right:-35vw}100%{right:0}}.Content .controller h3{margin:0;font-size:1.25vw;font-weight:600;line-height:2.5vw}.Content .controller h4{margin:0;font-size:0.9vw;font-weight:400;color:var(--p)}.Content .controller .content{-webkit-flex:1 1;flex:1 1;padding-top:2vw}.Content .controller .content .block{display:-webkit-flex;display:flex;-webkit-flex-flow:row nowrap;flex-flow:row nowrap;-webkit-align-items:center;align-items:center;margin-bottom:2vw}.Content .controller .content .block span:nth-child(2){-webkit-flex:1 1;flex:1 1}.Content .controller .content .block .control-btn{color:var(--b);background-color:var(--w);border:1px solid #E2DDD3;height:calc(var(--navw) * 0.35);padding-left:0.75vw;padding-right:0.75vw;margin-right:0.75vw;display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center;border-radius:0.4vw;font-size:0.9vw;font-weight:600;transition:background 0s}.Content .controller .content .block .control-btn.selected{background-color:var(--b);color:var(--w);transition:background 0.75s}.Content .controller .content .block .control-btn.disabled{color:var(--b);background-color:#E2DDD3}.Content .controller .content .block .control-btn.disabled:hover{cursor:default}.Content .controller .content .block .control-btn:hover{cursor:pointer}.Content .controller .content #c-ico{font-size:calc(var(--navw) * 0.35);width:1em;height:1em;color:var(--b)}.Content .controller .content span{margin-left:0.75vw;font-size:1vw;font-weight:500}.Content .controller .total{display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.Content .controller .total span{display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center}.Content .controller .total span:nth-child(1){font-size:0.9vw;font-weight:400}.Content .controller .total span:nth-child(2){font-size:1vw;font-weight:500}.Content .controller .confirm{width:100%;height:calc(var(--navw) * 0.5);background-color:var(--y);border-radius:0.5vw;display:-webkit-inline-flex;display:inline-flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:1vw;font-weight:600;margin-top:2vw}.Content .controller .confirm:hover{cursor:pointer}

.Team{padding-left:calc(var(--navw) * 0.5);padding-top:calc(var(--navw) * 0.25);-webkit-animation:0.25s ease-out 0s 1 fadeIn;animation:0.25s ease-out 0s 1 fadeIn}@-webkit-keyframes fadeIn{0%{opacity:0}100%{opacity:1}}@keyframes fadeIn{0%{opacity:0}100%{opacity:1}}.Team h1{margin:0;font-size:1.75vw;font-weight:600;line-height:2.5vw}.Team h2{margin:0;font-size:1vw;font-weight:400;color:var(--p)}.Team .member-wrapper{position:absolute;top:calc(var(--navw) * 1.2);min-width:100px;min-height:100px}.Team .member-box{position:relative;float:left;margin-right:1vw;width:calc(var(--navw) * 2);height:calc(var(--navw) * 2.1);margin-bottom:2vw}.Team .pic-wrapper{position:fixed;z-index:2;background-color:azure;margin-left:calc(var(--navw) * 0.5);width:calc(var(--navw));height:calc(var(--navw));border-radius:100%;overflow:hidden;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.Team .pic-wrapper img{position:absolute;-webkit-flex-shrink:0;flex-shrink:0}.Team .pic-wrapper #one{width:120%}.Team .pic-wrapper #two{width:130%}.Team .pic-wrapper #three{top:0;left:-40%;width:160%}.Team .pic-wrapper #four{top:-62.5%;left:-162.5%;width:400%}.Team .pic-wrapper #five{top:-18.5%;left:-25%;width:160%}.Team .pic-wrapper #six{top:-7.5%;left:-25%;width:125%}.Team .member-info{position:fixed;z-index:1;margin-top:calc(var(--navw) * 0.5);height:calc(var(--navw) * 0.975);width:calc(var(--navw) * 2);text-align:center;color:var(--w);background-color:var(--b);border-radius:.5vw;padding-top:calc(var(--navw) * 0.625);box-shadow:0px 4px 4px rgba(0,0,0,0.1)}.Team .member-info p{margin:0;font-size:0.9vw;line-height:1.6vw}.Team .member-info p:nth-child(1){font-weight:500}.Team .member-info p:nth-child(2){font-weight:500}

